<template>
    <div class="trackInfo">
        <div class="Info">
            <div class="cabecera">
                <img class="imagenCD" :src="infoTrack.imagen" >
                <div class="dataCD">
                    <div class="titulo">
                        {{infoTrack.nombre}}
                    </div>
                    <div class="codigo">
                        {{infoTrack.codigo}}
                    </div>
                    <div class="infoCD">
                        <div>Character: {{infoTrack.character}}</div>
                        <div>Realese  : {{infoTrack.date}}</div>
                        <div>Tags     : {{infoTrack.tags}}</div>
                    </div>
                </div>                    
            </div>
            <div class="cuerpo">
                <div class="headerTable">
                    <div>Track</div>
                    <div>Format</div>
                    <div>Subtittle ENG</div>
                    <div>Play</div>
                </div>   
                <div class="bodyTable">
                    <div  v-for="(i,index) in infoTrack.tracks" :key="index" > 
                        <div v-if=" i.mp3!== '' " class="fila" >
                            <div>{{i.num}}</div>
                            <div >
                                <select class="formatos" v-model="$store.state.audioSelection.formato" >
                                    <option v-if="i.mp3!== ''" value="MP3" selected> MP3 </option>
                                    <option v-if="i.flac!== ''" value="FLAC"> FLAC </option>
                                    <option v-if="i.wav!== ''" value="WAV"> WAV </option>
                                </select>
                            </div>
                            <div class="subtittle">
                                <div v-if="i.sub!== ''" value="ENG"> Yes </div>
                                <div v-else> No </div>
                            </div>
                            <div>
                                <router-link :to="{ name:'player', params:{id1:infoTrack.codigo,id2:i.num,id3:$store.state.audioSelection.formato} }">
                                    <i class="fas fa-play icono"></i>
                                </router-link>
                            </div>
                        </div>

                    </div>                        
                </div>

            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TrackInfo',
    data(){
        return{
            infoTrack:[]
        }
    },
    created:
        function(){
            var track_url = 'https://spreadsheets.google.com/feeds/list/1NP0TDKko58E9tW0j0VaDfR5VZe-b-FQcv-MpPu2u6EU/1/public/values?alt=json'
            fetch(track_url)
            .then(res => res.json())
            .then(data =>{
                for (let ind of data.feed.entry){
                    if(this.$route.params.id == ind.gsx$code1.$t){
                        let temp={
                        "codigo" : ind.gsx$code1.$t,
                        "nombre" : ind.gsx$name.$t,
                        "imagen" : ind.gsx$cdimg.$t, 
                        "character" : ind.gsx$character.$t, 
                        "available" : ind.gsx$available.$t, 
                        "date" : ind.gsx$date.$t,
                        "tags" : ind.gsx$tags.$t,
                        "tracks" : [
                            { "num":1, "mp3": ind.gsx$track1mp3.$t,"flac": ind.gsx$track1flac.$t,"wav": ind.gsx$track1wav.$t,"sub":ind.gsx$track1sub.$t},
                            { "num":2, "mp3": ind.gsx$track2mp3.$t,"flac": ind.gsx$track2flac.$t,"wav": ind.gsx$track2wav.$t,"sub":ind.gsx$track2sub.$t},
                            { "num":3, "mp3": ind.gsx$track3mp3.$t,"flac": ind.gsx$track3flac.$t,"wav": ind.gsx$track3wav.$t,"sub":ind.gsx$track3sub.$t},
                            { "num":4, "mp3": ind.gsx$track4mp3.$t,"flac": ind.gsx$track4flac.$t,"wav": ind.gsx$track4wav.$t,"sub":ind.gsx$track4sub.$t},
                            { "num":5, "mp3": ind.gsx$track5mp3.$t,"flac": ind.gsx$track5flac.$t,"wav": ind.gsx$track5wav.$t,"sub":ind.gsx$track5sub.$t},
                            { "num":6, "mp3": ind.gsx$track6mp3.$t,"flac": ind.gsx$track6flac.$t,"wav": ind.gsx$track6wav.$t,"sub":ind.gsx$track6sub.$t},
                            { "num":7, "mp3": ind.gsx$track7mp3.$t,"flac": ind.gsx$track7flac.$t,"wav": ind.gsx$track7wav.$t,"sub":ind.gsx$track7sub.$t},
                            { "num":8, "mp3": ind.gsx$track8mp3.$t,"flac": ind.gsx$track8flac.$t,"wav": ind.gsx$track8wav.$t,"sub":ind.gsx$track8sub.$t},
                            { "num":9, "mp3": ind.gsx$track9mp3.$t,"flac": ind.gsx$track9flac.$t,"wav": ind.gsx$track9wav.$t,"sub":ind.gsx$track9sub.$t},
                            { "num":10, "mp3": ind.gsx$track10mp3.$t,"flac": ind.gsx$track10flac.$t,"wav": ind.gsx$track10wav.$t,"sub":ind.gsx$track10sub.$t},
                            { "num":11, "mp3": ind.gsx$track11mp3.$t,"flac": ind.gsx$track11flac.$t,"wav": ind.gsx$track11wav.$t,"sub":ind.gsx$track11sub.$t},
                            { "num":12, "mp3": ind.gsx$track12mp3.$t,"flac": ind.gsx$track12flac.$t,"wav": ind.gsx$track12wav.$t,"sub":ind.gsx$track12sub.$t},
                            { "num":13, "mp3": ind.gsx$track13mp3.$t,"flac": ind.gsx$track13flac.$t,"wav": ind.gsx$track13wav.$t,"sub":ind.gsx$track13sub.$t},
                            { "num":14, "mp3": ind.gsx$track14mp3.$t,"flac": ind.gsx$track14flac.$t,"wav": ind.gsx$track14wav.$t,"sub":ind.gsx$track14sub.$t},
                            { "num":15, "mp3": ind.gsx$track15mp3.$t,"flac": ind.gsx$track15flac.$t,"wav": ind.gsx$track15wav.$t,"sub":ind.gsx$track15sub.$t},
                            { "num":16, "mp3": ind.gsx$track16mp3.$t,"flac": ind.gsx$track16flac.$t,"wav": ind.gsx$track16wav.$t,"sub":ind.gsx$track16sub.$t},
                            { "num":17, "mp3": ind.gsx$track17mp3.$t,"flac": ind.gsx$track17flac.$t,"wav": ind.gsx$track17wav.$t,"sub":ind.gsx$track17sub.$t},
                            { "num":18, "mp3": ind.gsx$track18mp3.$t,"flac": ind.gsx$track18flac.$t,"wav": ind.gsx$track18wav.$t,"sub":ind.gsx$track18sub.$t},
                            { "num":19, "mp3": ind.gsx$track19mp3.$t,"flac": ind.gsx$track19flac.$t,"wav": ind.gsx$track19wav.$t,"sub":ind.gsx$track19sub.$t},
                            { "num":20, "mp3": ind.gsx$track20mp3.$t,"flac": ind.gsx$track20flac.$t,"wav": ind.gsx$track20wav.$t,"sub":ind.gsx$track20sub.$t}
                        ]
                        }
                    this.infoTrack = temp;
                    }
                }
            })
        }   
}
</script>

<style scoped>
    .trackInfo{
        width: 100%;

        background-color: rgb(33, 37, 41);
        
    }
    .Info{
        background-color: white;
        width: 100%;
        max-width: 960px;
        height: 100vh;
        margin: 0 auto 0 auto;
        
    }
    .imagenCD{
        margin:  10px auto 0 auto;
        width: 80%;
        border-radius: 10px; 
    }
    .dataCD{
        margin-top:  10px;
    }
        .titulo{
            font-size: 24px;
            color: rgb(33, 37, 41);
            margin-bottom: 10px;
        }

        .codigo{
            font-size: 22px;
            color: rgb(33, 37, 41);
            margin-bottom: 10px;
        }
        .infoCD{
            font-size: 16px;
            color: gray;
        }
    .headerTable{
        max-width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        text-align: center;
        color: white;
        padding: 15px;
        margin-bottom: 30px;
        margin-top: 30px;
        background-color: rgba(33, 37, 41,1);
        font-size: 18px;
    }
    .fila{
        padding: 15px;
        max-width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        text-align: center; 
        margin-bottom: 30px; 
        font-size: 20px;
        color: rgb(33, 37, 41);
    }
    .cabecera{
        
        display: grid;
        max-width: 100%;
        /* text-align: center; */
        grid-template-columns: 40% 60%;
        margin-bottom: 20px;
    }
    .formatos{
        padding: 4px 8px 4px 8px;
        margin: 0px 8px 0px 8px;
        border-radius: 4px; 
        background-color: rgb(233, 236, 239);
        color: rgb(33, 37, 41);
        border: 0;
        font-size: 16px;
        outline: 0px;
    }
    
</style>

