<template>
    <div class="Player">
        <div class="Info">
            <div class="cabecera">
                <img class="imagenCD" :src="infoTrack.imagen" >
                <div class="dataCD">
                    <div class="titulo">
                        {{infoTrack.nombre}}
                    </div>
                    <div class="codigo">
                        {{infoTrack.codigo}}
                    </div>
                </div>                    
            </div>
            <div class="Subtitulos">
                {{audioInfo}}
            </div>

        </div>
        <div class="controles">
            <div class="contenedor">
                <div class="avance">

                </div>
                <div class="botones">
                    <div class="subtitulos">
                        <i class="fas fa-external-link-alt iconoB"></i>
                    </div>
                    <div class="atras">
                        <i class="fas fa-angle-double-left iconoA"></i>
                    </div>
                    <div class="play" @click="reproducir">
                        <i class="fas fa-play iconoP"></i>
                    </div>
                    <div class="adelante">
                        <i class="fas fa-angle-double-right iconoA"></i>
                    </div>
                    <div class="volumen">
                        <i class="fas fa-volume-up iconoB"></i>
                    </div>
                    <div class="time">
                        24:05/50:63 

                    </div>
                </div>

            </div>
        </div>
    </div>    
</template>
<script>
export default {
    name: 'Player',
    data(){
        return{
            audioURL:"",
            audioData:{},
            infoTrack:[],
            currentTime:0
        }
    },
    created:
        function(){
            var track_url = 'https://spreadsheets.google.com/feeds/list/1NP0TDKko58E9tW0j0VaDfR5VZe-b-FQcv-MpPu2u6EU/1/public/values?alt=json'
            fetch(track_url)
            .then(res => res.json())
            .then(data =>{
                for (let ind of data.feed.entry){
                    if(this.$route.params.id1 == ind.gsx$code1.$t){
                        let temp={
                        "codigo" : ind.gsx$code1.$t,
                        "nombre" : ind.gsx$name.$t,
                        "imagen" : ind.gsx$cdimg.$t, 
                        "character" : ind.gsx$character.$t, 
                        "available" : ind.gsx$available.$t, 
                        "date" : ind.gsx$date.$t,
                        "tags" : ind.gsx$tags.$t,
                        "tracks" : [
                            { "num":1, "mp3": ind.gsx$track1mp3.$t,"flac": ind.gsx$track1flac.$t,"wav": ind.gsx$track1wav.$t,"sub":ind.gsx$track1sub.$t},
                            { "num":2, "mp3": ind.gsx$track2mp3.$t,"flac": ind.gsx$track2flac.$t,"wav": ind.gsx$track2wav.$t,"sub":ind.gsx$track2sub.$t},
                            { "num":3, "mp3": ind.gsx$track3mp3.$t,"flac": ind.gsx$track3flac.$t,"wav": ind.gsx$track3wav.$t,"sub":ind.gsx$track3sub.$t},
                            { "num":4, "mp3": ind.gsx$track4mp3.$t,"flac": ind.gsx$track4flac.$t,"wav": ind.gsx$track4wav.$t,"sub":ind.gsx$track4sub.$t},
                            { "num":5, "mp3": ind.gsx$track5mp3.$t,"flac": ind.gsx$track5flac.$t,"wav": ind.gsx$track5wav.$t,"sub":ind.gsx$track5sub.$t},
                            { "num":6, "mp3": ind.gsx$track6mp3.$t,"flac": ind.gsx$track6flac.$t,"wav": ind.gsx$track6wav.$t,"sub":ind.gsx$track6sub.$t},
                            { "num":7, "mp3": ind.gsx$track7mp3.$t,"flac": ind.gsx$track7flac.$t,"wav": ind.gsx$track7wav.$t,"sub":ind.gsx$track7sub.$t},
                            { "num":8, "mp3": ind.gsx$track8mp3.$t,"flac": ind.gsx$track8flac.$t,"wav": ind.gsx$track8wav.$t,"sub":ind.gsx$track8sub.$t},
                            { "num":9, "mp3": ind.gsx$track9mp3.$t,"flac": ind.gsx$track9flac.$t,"wav": ind.gsx$track9wav.$t,"sub":ind.gsx$track9sub.$t},
                            { "num":10, "mp3": ind.gsx$track10mp3.$t,"flac": ind.gsx$track10flac.$t,"wav": ind.gsx$track10wav.$t,"sub":ind.gsx$track10sub.$t},
                            { "num":11, "mp3": ind.gsx$track11mp3.$t,"flac": ind.gsx$track11flac.$t,"wav": ind.gsx$track11wav.$t,"sub":ind.gsx$track11sub.$t},
                            { "num":12, "mp3": ind.gsx$track12mp3.$t,"flac": ind.gsx$track12flac.$t,"wav": ind.gsx$track12wav.$t,"sub":ind.gsx$track12sub.$t},
                            { "num":13, "mp3": ind.gsx$track13mp3.$t,"flac": ind.gsx$track13flac.$t,"wav": ind.gsx$track13wav.$t,"sub":ind.gsx$track13sub.$t},
                            { "num":14, "mp3": ind.gsx$track14mp3.$t,"flac": ind.gsx$track14flac.$t,"wav": ind.gsx$track14wav.$t,"sub":ind.gsx$track14sub.$t},
                            { "num":15, "mp3": ind.gsx$track15mp3.$t,"flac": ind.gsx$track15flac.$t,"wav": ind.gsx$track15wav.$t,"sub":ind.gsx$track15sub.$t},
                            { "num":16, "mp3": ind.gsx$track16mp3.$t,"flac": ind.gsx$track16flac.$t,"wav": ind.gsx$track16wav.$t,"sub":ind.gsx$track16sub.$t},
                            { "num":17, "mp3": ind.gsx$track17mp3.$t,"flac": ind.gsx$track17flac.$t,"wav": ind.gsx$track17wav.$t,"sub":ind.gsx$track17sub.$t},
                            { "num":18, "mp3": ind.gsx$track18mp3.$t,"flac": ind.gsx$track18flac.$t,"wav": ind.gsx$track18wav.$t,"sub":ind.gsx$track18sub.$t},
                            { "num":19, "mp3": ind.gsx$track19mp3.$t,"flac": ind.gsx$track19flac.$t,"wav": ind.gsx$track19wav.$t,"sub":ind.gsx$track19sub.$t},
                            { "num":20, "mp3": ind.gsx$track20mp3.$t,"flac": ind.gsx$track20flac.$t,"wav": ind.gsx$track20wav.$t,"sub":ind.gsx$track20sub.$t}
                        ]
                        }
                        for(let ind of temp.tracks){
                            if(this.$route.params.id2 == ind.num ){

                                if(this.$route.params.id3 === "MP3"){
                                    this.audioURL= ind.mp3
                                }else if(this.$route.params.id3 === "FLAC"){
                                    this.audioURL= ind.flac

                                }else if(this.$route.params.id3 === "WAV"){
                                    this.audioURL= ind.wav

                                }
                                var x = document.createElement("AUDIO");
                                x.setAttribute("src",this.audioURL);
                                x.setAttribute("id","audio_id");
                                x.setAttribute("ref","audio");
                                x.setAttribute("preload","auto");
                                document.body.appendChild(x);

                            }
                        }
                        this.infoTrack = temp;                        
                    }                    
                }
            })


        },
    methods:{
        reproducir(){
        var x = document.getElementById("audio_id");    
        x.play()
        }
    },
    update:{
        audioInfo:function(){
            if (document.getElementById("audio_id") == null){
                return 0
            } else{
                this.currentTime = this.$refs.audio.currentTime
            }
        }
    }        
}           
</script>                

<style scoped>
    .Player{
        width: 100%;

        background-color: rgb(33, 37, 41);
        
    }
    .Info{
        background-color: white;
        width: 100%;
        max-width: 960px;
        height: 100vh;
        margin: 0 auto 0 auto;
        
    }
    .cabecera{
        
        display: grid;
        max-width: 100%;
        /* text-align: center; */
        grid-template-columns: 40% 60%;
        margin-bottom: 20px;
    }
        .imagenCD{
            margin:  20px auto 0 auto;
            width: auto;
            height: 15vh;
            min-height: 120px;
            border-radius: 10px; 
        }
        .dataCD{
            margin-top:  25px;
        }
            .titulo{
                font-size: 24px;
                color: rgb(33, 37, 41);
                margin-bottom: 10px;
            }

            .codigo{
                font-size: 22px;
                color: rgb(33, 37, 41);
                margin-bottom: 10px;
            }
            .infoCD{
                font-size: 16px;
                color: gray;
            }
    .controles{
        position: fixed;
        bottom: 0;
        height: 100px;
        width: 100vw;
        
    }            
    .contenedor{
        max-width: 960px;
        width: 100%;
        background-color: rgb(33, 37, 41);
        height: 100px;
        margin: 0 auto 0 auto;
    }
    .botones{
        display: grid;
        grid-template-columns: 2fr 1fr 1fr 1fr 1.5fr 1.5fr;
        text-align: center;
        margin-top: 20px;
        font-size: 20px;
    }
    .avance{
        height: 10px;
        background-color: gray;
    }
    .iconoP{
        font-size: 28px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        padding: 12px 12px 12px 16px;
        color: white;
    }
    .iconoP:hover{
        
        background-color: whitesmoke;
        color: rgb(33, 37, 41);
    }
    .iconoA{
        margin: 3px 0 0 0;
        font-size: 20px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        padding: 12px 14px 12px 14px;
        color: whitesmoke;
    }
    .iconoA:hover{
        background-color: whitesmoke;
        color: rgb(33, 37, 41);
    }
    .iconoB{
        margin: 3px 0 0 0;
        font-size: 20px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        padding: 12px 12px 12px 12px;
        color: whitesmoke;
    }
    .iconoB:hover{
        background-color: whitesmoke;
        color: rgb(33, 37, 41);
    }
    .time{
        margin: 1px 0 0 0;
        font-size: 20px;
        padding: 12px 12px 12px 12px;
        color: gray;
        text-align: left;
        color:white;
    }
</style>